import React from "react";
import {
  Grid,
  Typography,
  AppBar,
  Drawer,
  IconButton,
  Toolbar,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Box,
  useMediaQuery,
} from "@mui/material";
import { useTheme } from "@mui/material/styles";
import {
  Menu as MenuIcon,
  Event as EventIcon,
  Description as DescriptionIcon,
  Business as BusinessIcon,
  School as SchoolIcon,
  Close as CloseIcon,
  People as PeopleIcon,
} from "@mui/icons-material";
import Link from "./Link";
import LogoIcon from "../images/logo.svg";

const drawerWidth = 240;

export default function Header(props) {
  const { window } = props;
  const theme = useTheme();
  const isLgUp = useMediaQuery(theme.breakpoints.up("lg"));
  const isMdDown = useMediaQuery(theme.breakpoints.down("md"));
  const isSmDown = useMediaQuery(theme.breakpoints.down("sm"));
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };
  const container =
    window !== undefined ? () => window().document.body : undefined;

  const ResponsiveDrawer = (
    <>
      <List>
        <ListItem onClick={handleDrawerToggle} button>
          <ListItemIcon>
            <CloseIcon />
          </ListItemIcon>
        </ListItem>
        <Divider />
        <Link to="/events">
          <ListItem button>
            <ListItemIcon>
              <EventIcon />
            </ListItemIcon>
            <ListItemText primary="" />
          </ListItem>
        </Link>
        <Link to="/resources">
          <ListItem button>
            <ListItemIcon>
              <SchoolIcon />
            </ListItemIcon>
            <ListItemText primary="" />
          </ListItem>
        </Link>
        <Link to="/companies">
          <ListItem button>
            <ListItemIcon>
              <BusinessIcon />
            </ListItemIcon>
            <ListItemText primary="" />
          </ListItem>
        </Link>
        <Link to="/reports">
          <ListItem button>
            <ListItemIcon>
              <DescriptionIcon />
            </ListItemIcon>
            <ListItemText primary="" />
          </ListItem>
        </Link>
        <Link to="/contributors">
          <ListItem button>
            <ListItemIcon>
              <PeopleIcon />
            </ListItemIcon>
            <ListItemText primary="" />
          </ListItem>
        </Link>
      </List>
    </>
  );

  return (
    <Box sx={{ display: "flex" }}>
      <AppBar 
        position="fixed"
        sx={{
          background: "linear-gradient(135deg, #232F3E 0%, #37475A 100%)",
          boxShadow: "0 4px 20px rgba(0,0,0,0.1)",
          backdropFilter: "blur(10px)",
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{
              display: { lg: "none" },
            }}
          >
            <MenuIcon />
          </IconButton>
          <Grid container justifyContent="space-between" alignItems="center">
            <Grid item>
              <Typography
                variant="h6"
                sx={{ 
                  fontSize: "1.1rem", 
                  fontWeight: 700,
                  letterSpacing: "0.02em",
                  transition: "all 0.3s ease",
                  "&:hover": {
                    transform: "scale(1.02)",
                  },
                }}
              >
                <Link
                  to="/"
                  color="inherit"
                  sx={{ 
                    display: "flex", 
                    alignItems: "stretch",
                    gap: 1,
                    transition: "opacity 0.3s ease",
                    "&:hover": {
                      opacity: 0.9,
                    },
                  }}
                >
                  <img
                    width="26"
                    height="26"
                    alt="Amplify"
                    src={LogoIcon}
                    style={{
                      filter: "drop-shadow(0 2px 4px rgba(0,0,0,0.2))",
                    }}
                  />
                  {!isSmDown && (
                    <Box sx={{ display: { lg: "inline" } }}>
                      Amplify Japan User Group
                    </Box>
                  )}
                  <span role="img" aria-label="">
                    
                  </span>
                </Link>
              </Typography>
            </Grid>
            <Grid item>
              {!isMdDown && (
                <Grid
                  container
                  justifyContent="flex-end"
                  alignItems="center"
                  spacing={1}
                >
                  <Grid item>
                    <Link 
                      to="/events"
                      sx={{
                        transition: "all 0.3s ease",
                        padding: "8px 12px",
                        borderRadius: "8px",
                        "&:hover": {
                          backgroundColor: "rgba(255, 255, 255, 0.1)",
                          transform: "translateY(-2px)",
                        },
                      }}
                    >
                      <EventIcon sx={{ verticalAlign: "middle", mr: 0.5 }} />
                      
                    </Link>
                  </Grid>
                  <Grid item>
                    <Link 
                      to="/resources"
                      sx={{
                        transition: "all 0.3s ease",
                        padding: "8px 12px",
                        borderRadius: "8px",
                        "&:hover": {
                          backgroundColor: "rgba(255, 255, 255, 0.1)",
                          transform: "translateY(-2px)",
                        },
                      }}
                    >
                      <SchoolIcon sx={{ verticalAlign: "middle", mr: 0.5 }} />
                      
                    </Link>
                  </Grid>
                  <Grid item>
                    <Link 
                      to="/reports"
                      sx={{
                        transition: "all 0.3s ease",
                        padding: "8px 12px",
                        borderRadius: "8px",
                        "&:hover": {
                          backgroundColor: "rgba(255, 255, 255, 0.1)",
                          transform: "translateY(-2px)",
                        },
                      }}
                    >
                      <DescriptionIcon sx={{ verticalAlign: "middle", mr: 0.5 }} />
                      
                    </Link>
                  </Grid>
                  <Grid item>
                    <Link 
                      to="/contributors" 
                      color="inherit"
                      sx={{
                        transition: "all 0.3s ease",
                        padding: "8px 12px",
                        borderRadius: "8px",
                        "&:hover": {
                          backgroundColor: "rgba(255, 255, 255, 0.1)",
                          transform: "translateY(-2px)",
                        },
                      }}
                    >
                      <PeopleIcon sx={{ verticalAlign: "middle", mr: 0.5 }} />
                      
                    </Link>
                  </Grid>
                  <Grid item>
                    <Link 
                      to="/companies" 
                      color="inherit"
                      sx={{
                        transition: "all 0.3s ease",
                        padding: "8px 12px",
                        borderRadius: "8px",
                        "&:hover": {
                          backgroundColor: "rgba(255, 255, 255, 0.1)",
                          transform: "translateY(-2px)",
                        },
                      }}
                    >
                      <BusinessIcon sx={{ verticalAlign: "middle", mr: 0.5 }} />
                      
                    </Link>
                  </Grid>
                </Grid>
              )}
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      {!isLgUp && (
        <Box component="nav" sx={{ width: drawerWidth, flexShrink: 0 }}>
          <Drawer
            container={container}
            variant="temporary"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            sx={{
              "& .MuiDrawer-paper": {
                width: drawerWidth,
              },
            }}
            ModalProps={{
              keepMounted: true,
            }}
          >
            {ResponsiveDrawer}
          </Drawer>
        </Box>
      )}
      <Box
        sx={{
          marginBottom: { xs: "56px", sm: "64px" },
        }}
      />
    </Box>
  );
}
